.page {
  background-color: #000;
  font-family: "Inter", "Arial", "sans-serif";
  margin: 0 auto;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
